html {
    height: 100vh;
    overflow: scroll;
    padding: 1.5vw;
}

body,
#root {
    height: 100%;
}

.App {
    display: flex;
    height: 100%;
    flex-wrap: wrap;
    margin: 0;
}

#game-canvas,
#slide-deck {
    flex: 1;
}

.frame-container {
    flex: 1;
    height: 81vh;
    display: flex;
}

.tailspin-loader {
    align-items: center;
    justify-content: center;
    min-width: 100%;
    min-height: 100%;
    background-color: rgb(20, 20, 20);
}

.break {
    flex-basis: 100%;
    height: 0;
}

.polls {
    background: white;
    position: absolute;
    top: 0;
    left: 0;
    margin: 20px;
    z-index: 2;
    min-width: 100vw;
    min-height: 71vh;
}

.poll-graph-container
{
    padding: 40px;
    display: flex;
    justify-content: center;
    align-content: center;
    height: 71vh;
}

.poll-btns-container {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
}

.poll-btns-title { 
    flex: 0 0 100%;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 16px;
}
